ul {
    flex-wrap: nowrap;
    padding: 0;
    margin: 0;
}


.screenContainer {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.contentContainer {
    display:flex;
    flex-direction: column;
    padding:20px;
    overflow: scroll;
}

.navbar {
    -webkit-box-shadow: 0px -4px 10px rgba(50, 50, 50, 0.3);
    -moz-box-shadow: 0px -4px 10px rgba(50, 50, 50, 0.3);
    box-shadow: 0px -4px 10px rgba(50, 50, 50, 0.3);
}

.navbar ul {
    list-style: none;
    align-items: center;
    display:flex;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    padding: 0;
    margin: 0;
    margin-bottom: 2rem;
    margin-top: 1rem;
    
}

.checkBoxContainer {
    border-radius: 5px;
    flex-grow: 1;
    padding:10px 5px;
    margin-right:10px;
    -webkit-box-shadow: 0px 0px 4px rgba(50, 50, 50, 0.5);
    -moz-box-shadow: 0px 0px 4px rgba(50, 50, 50, 0.5);
    box-shadow: 0px 0px 4px rgba(50, 50, 50, 0.5);
}
.cards{
    border: 1px solid rgb(220,220,220);
    -webkit-box-shadow: 3px 2px 10px rgba(50, 50, 50, 0.2);
    -moz-box-shadow: 3px 2px 10px rgba(50, 50, 50, 0.2);
    box-shadow: 3px 2px 10px rgba(50, 50, 50, 0.2);
    background-image: linear-gradient(to bottom right, rgb(240,240,240), rgb(244,244,244));
}

.navbar li{
    list-style: none;
    border-color: rgb(133, 133, 133);
    border-radius: 6px;
    padding:10px;
    -webkit-box-shadow: 0px 0px 8px rgba(50, 50, 50, 0.6);
    -moz-box-shadow: 0px 0px 8px rgba(50, 50, 50, 0.6);
    box-shadow: 0px 0px 8px rgba(50, 50, 50, 0.6);
}

.recordBox {
    display: flex;
    overflow:scroll;
    border: 1px solid rgb(200,200,200);
    flex:1;
    align-content: stretch;
    align-items: center;
    margin-bottom: 5px;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 4px rgba(50, 50, 50, 0.2);
    -moz-box-shadow: 0px 0px 4px rgba(50, 50, 50, 0.2);
    box-shadow: 0px 0px 4px rgba(50, 50, 50, 0.2);
}
.recordBox ul{
    display: flex;
    /* flex-grow: 1; */
}
.recordBox li {
    padding:10px;
    border-right:1px solid rgb(200,200,200);
    list-style: none;
    white-space: nowrap;
}

input[type="checkbox"]{
    margin:0px;
    padding:0px;
}
input[type="submit"]{
    margin-top:30px;
    padding:10px 60px;
    border:0px;
    background-color: rgb(26, 156, 104);
    color:white;
    border-radius: 5px;
    font-size: larger;
    -webkit-box-shadow: 0px 0px 8px rgba(12, 55, 14, 0.3);
    -moz-box-shadow: 0px 0px 8px rgba(12, 55, 14, 0.3);
    box-shadow: 0px 0px 8px rgba(12, 55, 14, 0.3);
}
button{
    margin-top:30px;
    padding:10px 50px;
    border:0px;
    background-color: rgb(26, 156, 104);
    color:white;
    border-radius: 5px;
    font-size: medium;
    -webkit-box-shadow: 0px 0px 8px rgba(12, 55, 14, 0.3);
    -moz-box-shadow: 0px 0px 8px rgba(12, 55, 14, 0.3);
    box-shadow: 0px 0px 8px rgba(12, 55, 14, 0.3);
}

a {
    color: black;
    text-decoration: none; /* no underline */
  }
h1 {
    text-align: center;
    margin:20px 0px 40px;
}
h2 {
    text-align: center;
    margin:10px 10px 20px;
}

.modalContainer {
    background-color: rgba(0,0,0, 0.78);
    position:absolute;
    left:0;
    top:0;
    right:0;
    bottom:0;
}

.modal {
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
    border:2px solid gray;
    padding: 10px 20px;
    font-size: medium;
    position:absolute;
    width: 80vw;
    left:10vw;
    top:20vh;
    -webkit-box-shadow: 0px 0px 30px rgba(0,0,0, 0.65);
    -moz-box-shadow: 0px 0px 30px rgba(0,0,0, 0.65);
    box-shadow: 0px 0px 30px rgba(0,0,0, 0.65);
}